import React from "react";
import BaseComponent from "../baseComponent";
import Sidebar from "../dashboard/sidebar";
import Header from "../header/header";
import { Row } from "simple-flexbox";
import Footer from "../Footer";
import { connect } from "react-redux";
import UpdateProfileComponent from "./updateProfile";
import Utility from "../../utility";
import { contractManagementService } from "../../services";
import toast, { Toaster } from "react-hot-toast";
import { history } from "../../managers/history";
import ScreenSizeDetector from "screen-size-detector";

const screen = new ScreenSizeDetector();

class UpdateProfile extends BaseComponent {
  constructor(props) {
    super(props);
    this.state = {
      isUpdating: false,
    };
  }

  componentDidMount() {
    
  }

  updateDeployedXrc20Token = async (updatedData) => {
    this.setState({
      isUpdating: true,
    })

    let contractAddress;
    if (this.props?.user?.accountDetails?.network === "XDC Apothem Testnet") {
      contractAddress =
        this.props?.location?.state?.deolyedTokenDetails?.smartContractAddress.replace(
          /0x/,
          "xdc"
        );
    } else {
      contractAddress =
        this.props?.location?.state?.deolyedTokenDetails?.smartContractAddress;
    }
    let requestData = {
      tokenId: this.props?.location?.state?.deolyedTokenDetails?.id,
      tokenOwner: this.props?.location?.state?.deolyedTokenDetails?.tokenOwner,
      smartContractAddress: contractAddress,
      network: this.props?.user?.accountDetails?.network,

      website: updatedData.website ? updatedData.website : updatedData.website === "" ? "" : this.props?.location?.state?.deolyedTokenDetails.website,
      twitter: updatedData.twitter ? updatedData.twitter : updatedData.twitter === "" ? "" : this.props?.location?.state?.deolyedTokenDetails.twitter,
      telegram: updatedData.telegram ? updatedData.telegram : updatedData.telegram === "" ? "" : this.props?.location?.state?.deolyedTokenDetails.telegram,
      symbolUrl: updatedData.image ? updatedData.image : this.props?.location?.state?.deolyedTokenDetails.tokenImage,
      email: updatedData.email ? updatedData.email : updatedData.email === "" ? "" : this.props?.location?.state?.deolyedTokenDetails.email,
      facebook: updatedData.facebook ? updatedData.facebook : updatedData.facebook === "" ? "" : this.props?.location?.state?.deolyedTokenDetails.facebook,
      linkedIn: updatedData.linkedIn ? updatedData.linkedIn : updatedData.linkedIn === "" ? "" : this.props?.location?.state?.deolyedTokenDetails.linkedIn,
      reddit: updatedData.reddit ? updatedData.reddit : updatedData.reddit === "" ? "" : this.props?.location?.state?.deolyedTokenDetails.reddit,
      coinGecko: updatedData.coinGecko ? updatedData.coinGecko : updatedData.coinGecko === "" ? "" : this.props?.location?.state?.deolyedTokenDetails.coinGecko,
    };

    let [error, contractServiceResponse] = await Utility.parseResponse(
      contractManagementService.updateDeployedXrc20Token(requestData)
    );

    if (error || !contractServiceResponse) {
      toast.error("Unable To Update Details", {
        duration: 4000,
        position: "top-center",
      });
      this.setState({
        isUpdating: false,
      })
      return;
    }
    if (contractServiceResponse) {
      this.setState({
        isUpdating: false,
      })

      setTimeout(() => {
        toast.success("Details Updated Successfully", {
          duration: 4000,
          position: "top-center",
        });
      }, 2000);

      history.push({
        pathname: "/manage-contract-details",
        state: {
          id: this.props?.location?.state?.deolyedTokenDetails?.id,
        },
      });
    }
  };

  render() {
    return (
      <div>
        <Header />
        <div>
          <Toaster />
        </div>
        <Row>
          {screen.width >= 1024 ? <Sidebar /> : ""}
          <UpdateProfileComponent
            state={this.state}
            setUpdatedValues={this.setUpdatedValues}
            deolyedTokenDetails={
              this.props?.location?.state?.deolyedTokenDetails
            }
            updateDeployedXrc20Token={this.updateDeployedXrc20Token}
          />
        </Row>
        {screen.width <= 1023 ? <Footer /> : ""}
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return { user: state.user };
};

export default connect(mapStateToProps)(UpdateProfile);
